export interface ITextStyle {
	fontName: string;
	fontSize: number;
	isBold: boolean;
	isUnderlined: boolean;
	isStrikethrough: boolean;
	isItalic: boolean;
	color: string;
	stroke: string;
	strokeWidth: number;
	strokeColor: string;
	letterSpacing: number;
	alpha: number;
}

interface ITextObject {
	x: number;
	y: number;
}

interface ITextSection extends ITextObject {
	style: ITextStyle;
	text: string;
}

// const widthCache = new Map<ITextStyle, Map<string, number>>();
// const heightCache = new Map<ITextStyle, number>();

type Command = (style: ITextStyle, parameter?: string) => ITextStyle;

const tmpContext = document.createElement('canvas').getContext('2d');

export class TextRenderer {
	public static textCommands: Map<string, Command> = new Map<string, Command>([
		paramlessOp('i', (style: ITextStyle) => ({ ...style, isItalic: true })),
		paramlessOp('b', (style: ITextStyle) => ({ ...style, isBold: true })),
		paramlessOp('u', (style: ITextStyle) => ({ ...style, isUnderlined: true })),
		paramlessOp('s', (style: ITextStyle) => ({
			...style,
			isStrikethrough: true,
		})),
		paramlessOp('plain', (style: ITextStyle) => ({
			...style,
			isStrikethrough: false,
			isUnderlined: false,
			isBold: false,
			isItalic: false,
		})),
		relativeNumberOp('k', (style, relative, parameter) => ({
			...style,
			letterSpacing: relative ? style.letterSpacing + parameter : parameter,
		})),
		relativeNumberOp('alpha', (style, relative, parameter) => ({
			...style,
			alpha: relative ? style.alpha + parameter : parameter,
		})),
		[
			'font',
			(style, parameter?) => {
				return { ...style, fontName: parameter! };
			},
		],
		[
			'color',
			(style, parameter?) => {
				return { ...style, color: parameter! };
			},
		],
	]);

	public constructor(
		str: string,
		autoWrapWidth: number,
		baseStyle: ITextStyle
	) {
		const tokens = tokenize(str);
	}
}

export function tokenize(str: string): IToken[] {
	const tokens: IToken[] = [];
	let currentTokenState: TokenizerState = tokenStateNormal;
	while (currentTokenState !== tokenStateEnd) {
		[str, currentTokenState] = currentTokenState(tokens, str);
	}
	return tokens;
}

type TokenizerState = (
	contents: IToken[],
	str: string
) => [string, TokenizerState];

interface IToken {
	type: 'newline' | 'text' | 'command' | 'commandClose';
}
function tokenStateNormal(
	contents: IToken[],
	str: string
): [string, TokenizerState] {
	if (str.length === 0) {
		return ['', tokenStateEnd];
	}
	if (str[0] === '{') return [str, tokenStateCommand];
	if (str[0] === '\n') return [str, tokenStateNewLine];
	return [str, tokenText];
}

function tokenStateEnd(): [string, TokenizerState] {
	return null!;
}

interface ITextToken extends IToken {
	content: string;
}
function tokenText(contents: IToken[], str: string): [string, TokenizerState] {
	const endIdx = str.search(/[\{\n]/);
	const textContent = endIdx === -1 ? str : str.substr(0, endIdx);
	contents.push({
		type: 'text',
		content: textContent,
	} as ITextToken);
	return [endIdx === -1 ? '' : str.slice(endIdx), tokenStateNormal];
}

interface ICommandToken extends IToken {
	commandName: string;
	argument: string;
	type: 'command';
}
interface ICommandCloseToken extends IToken {
	commandName: string;
	type: 'commandClose';
}
function tokenStateCommand(
	contents: IToken[],
	str: string
): [string, TokenizerState] {
	const endOfCommandIdx = str.indexOf('}');
	const commandText = str.substr(1, endOfCommandIdx - 1).split('=');
	if (commandText[0][0] === '/') {
		if (commandText[1]) {
			throw new Error('Closing tags may not contain arguments');
		}
		contents.push({
			type: 'commandClose',
			commandName: commandText[0].slice(1),
		} as ICommandCloseToken);
	} else {
		contents.push({
			type: 'command',
			argument: commandText[1],
			commandName: commandText[0],
		} as ICommandToken);
	}
	return [str.slice(endOfCommandIdx + 1), tokenStateNormal];
}

function tokenStateNewLine(
	contents: IToken[],
	str: string
): [string, TokenizerState] {
	contents.push({ type: 'newline' });
	return [str.slice(1), tokenStateNormal];
}

function paramlessOp(
	name: string,
	op: (style: ITextStyle) => ITextStyle
): [string, Command] {
	return [
		name,
		(style: ITextStyle, parameter?: string) => {
			if (parameter) {
				throw new Error(`Operator '${name}' does not take any arguments.`);
			}
			return op(style);
		},
	];
}

function numberOp(
	name: string,
	op: (style: ITextStyle, param: number) => ITextStyle
): [string, Command] {
	return [
		name,
		(style: ITextStyle, parameter?: string) => {
			if (!parameter) throw new Error(`Operator '${name}' needs an argument.`);
			const num = Number(parameter);
			if (isNaN(num)) {
				throw new Error(`Operator '${name}' needs a numeric argument.`);
			}
			return op(style, num);
		},
	];
}

function relativeNumberOp(
	name: string,
	op: (style: ITextStyle, relative: boolean, param: number) => ITextStyle
): [string, Command] {
	return [
		name,
		(style: ITextStyle, parameter?: string) => {
			if (!parameter) throw new Error(`Operator '${name}' needs an argument.`);
			const relative = parameter[0] === '+' || parameter[0] === '-';
			const num = Number(parameter);
			if (isNaN(num)) {
				throw new Error(`Operator '${name}' needs a numeric argument.`);
			}
			return op(style, relative, num);
		},
	];
}
